@charset "utf-8";
$base-font-size:     16px;
$base-line-height:   1.5;

// BREAK POINT (MOBILE FIRST)
// $breakpoint-legacy:  375px;                          // Not in use
$breakpoint-xs:      576px; // < iPhone portrait        // 480 default
$breakpoint-sm:      768px; // > iPad                   // 768 default
$breakpoint-md:     1024px; // > iPad pro               // 1024 default
$breakpoint-lg:     1200px; // > Macbook Air
$breakpoint-xl:     1600px; // > Macbook Pro            // HD
$breakpoint-xxl:    1900px; // > Desktop                // QHD

// CONTENT WIDTH
$content-width-md:   960px; // Laptop
$content-width-lg:  1140px; // Laptop
$content-width-xl:  1250px; // Desktop
$content-width-xxl: 1400px; // Large screen 1440, 1396

// GUTTER
$column-gutter:       16px;
$column-gutter-lg:    24px;
$column-gutter-xxl:   28px;

// MODULAR SCALE
$ratio:              1.333;
$mscale_0:            1rem; // base
$mscale_1:  $mscale_0 * $ratio;
$mscale_2:  $mscale_1 * $ratio;
$mscale_3:  $mscale_2 * $ratio;
$mscale_4:  $mscale_3 * $ratio;
$mscale_5:  $mscale_4 * $ratio;
$mscale_6:  $mscale_5 * $ratio;
$mscale_7:  $mscale_6 * $ratio;
$mscale_8:  $mscale_7 * $ratio;
$mscale_9:  $mscale_8 * $ratio;
$mscale_n1: $mscale_0 / $ratio;
$mscale_n2: $mscale_n1 / $ratio;
$mscale_n3: $mscale_n2 / $ratio;
$mscale_n4: $mscale_n3 / $ratio;
$mscale_n5: $mscale_n4 / $ratio;

// COLOR THEME
$color-1:          #006dcc;
$color-2:          #4D7C8A;
$color-3:          #7F9C96;
$color-4:          #8FAD88;
$color-5:          #CBDF90;

// COLOR: UTILITY
$color-success:    #198754;
$color-danger:     #DA4F49;
$color-warning:    #FAA732;
$color-info:       #49AFCD;
$color-light:      #F5F5F5;
$color-dark:       #363636;

// COLOR: LINK & BUTTON
$link-color:       #1266F1;
$link-color-hover: #0a58ca;


// FONT FAMILY
$font-serif:       "Times New Roman", Times, serif;
$font-sans:        "Sora","SF Pro KR","Noto Sans KR","Apple SD Gothic Neo","나눔고딕","Nanum Gothic","맑은 고딕","Malgun Gothic",Arial,sans-serif;
$font-display:     "Sora","SF Pro KR","Noto Sans KR","Apple SD Gothic Neo","나눔고딕","Nanum Gothic","맑은 고딕","Malgun Gothic",Arial,sans-serif;
$font-button:      "Sora","SF Pro KR","Noto Sans KR","Apple SD Gothic Neo","나눔고딕","Nanum Gothic","맑은 고딕","Malgun Gothic",Arial,sans-serif;
$font-monospace:   "SFMono-Regular", "Menlo", "Monaco", "Consolas", "Liberation Mono", "Courier New", monospace;
$font-bold-weight: 600;
$font-system:      -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-korean:      Helvetica, Arial,"Apple SD Gothic Neo","나눔고딕","Nanum Gothic","맑은 고딕","Malgun Gothic",Applegothic,Dotum,sans-serif;


//  Helvetica, Arial,"나눔바른고딕","NanumBarunGothic","Apple SD Gothic Neo","나눔고딕","Nanum Gothic","Malgun Gothic",Applegothic,Dotum,sans-serif;
// "Times New Roman", Times, serif;
