// Enable mobile menu starting from:
$breakpoint-mobile-nav: 1200px;
$mobile-menu-width: 414px;



/* ===================================================
GLOBAL HEADER
==================================================== */
.globalHeader {
  box-sizing: border-box;
  min-height: 70px;
  padding: 1rem 0;
  position: relative;
  z-index: 2;
  // background: #f1f1f1;

  // Desktop
  @media screen and (min-width: $breakpoint-mobile-nav) {
    height: 120px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    // background: #FFF;
  }
//
}



/* NAVBAR LAYOUT
==================================================== */
.globalHeader__container {
  height: 100%;                   // Enables inner elements to use height 100%
}
.globalHeader__layout {
  position: relative;
  display: flex;
  justify-content: space-between; // Auto justify
  align-items: center;            // Vertical align middle
  height: 100%;
}



/* LOGO
==================================================== */
.globalHeaderLogo {
  color: #000;
  font-size: 20px;
  font-weight: 600;
  margin: 0;
  text-decoration: none;
  white-space: nowrap;

  &__link {
    max-width: 100px;
    display: inline-block;
  }

  &__image {
    height: auto;
    width: 100%;
  }
  // Desktop
  @media screen and (min-width: $breakpoint-mobile-nav) {
    &__link {
      max-width: 125px;
    }
  }
//
}



/* NAV MENU
==================================================== */
.globalNav {
  display: flex;

  &__header {
    display: none;
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    line-height: normal;
    list-style: none;
    margin-bottom: 0;
    padding-left: 0;
  }

  &__listItem {
    // padding: 0;
  }

  /* Nav link global style */
  a,
  a:hover,
  a:active {
    color: #000;
    display: block;
    text-decoration: none;
  }

  /* Nav link style */
  &__link {
    font-size: 18px;
    padding: 0.5rem 1.125rem;
  }

  @media screen and (min-width: $breakpoint-mobile-nav) {
    &__list {
      flex-direction: row;
      justify-content: center;
    }
    &__link {
      flex-direction: row;
      font-size: 16px;
      padding: 0.5rem 1rem;
    }
  }

  @media screen and (min-width: $breakpoint-xl) {
    &__link {
      // font-size: 18px;
      // padding: 0.5rem 1.5rem;
    }
  }

  @media screen and (min-width: $breakpoint-xxl) {
    &__link {
      // font-size: 20px;
    }
  }

//
}



/* MOBILE TOGGLE BUTTON
==================================================== */
// Visibility
.mobileMenuToggle {
  display: none;
  @media screen and (max-width: ($breakpoint-mobile-nav - 1) ) {
    & {display: block;}
  }


  // Button Style
  -webkit-appearance: none; // Reset default button style
  -webkit-border-radius:0;
  background: none;
  border-radius: 0;
  border: 0;
  box-sizing: border-box;
  color: #000;
  font-family: Sans-Serif;
  height: 44px;  // based on the Apple guideline for touch icon size
  overflow: hidden;
  padding: 0;
  position: relative;
  right: 0;
  top: 0;
  transition: all 100ms ease-in-out;
  width: 44px;
  z-index: 1046;

  &:hover,
  &:active {
    background: none;
  }

  // Copy
  span {
    display: none;
  }
  // Icon
  svg {
    width: 30px;
    height: auto;
  }


  // CLOSE BUTTON ICON
  &::after {
    content: "✕";
    position: fixed;
    top: 1.5rem;
    right: 1.5rem;
    margin-top: -3px;
    margin-right: -10px;

    display: flex;
    align-items: center;
    justify-content: center;

    font-family: Sans-Serif;
    font-size: 30px;
    font-weight: lighter;
    line-height: 0;
    color: #FFF;

    height: 44px;
    width: 44px;

    // transform: translate3d(-100px,0,0);
    // transition: all .1s cubic-bezier(1,.01,0,1);

    opacity: 0;
    transform: translateX(100px);
  }



  // Toggle animation
  #globalHeader.opened & {
    // margin-right: -100%;
    width: 0;

  }
  #globalHeader.opened &:after {
    opacity: 1;
    right: 1.5rem;
    transition: all 1s ease-in-out;
    transform: translateX(0px);
  }


//
}




/* MOBILE MENU
==================================================== */
@media screen and (max-width: ($breakpoint-mobile-nav - 1) ) {

  .globalNav {
    background-color: #712cf9;
    display: flex;
    flex-direction: column;
    outline: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    visibility: hidden;
    width: 100%;
    max-width: $mobile-menu-width;
    z-index: 1045;
    // box-shadow: 0 0 10px rgba(0,0,0,.8);
    transform: translateX(100%);
    transition: transform 300ms ease-in-out;


    // Opened
    #globalHeader.opened & {
      visibility: visible;
      transform: translateX(0%);
    }


  // Nav link color
  a,
  a:hover,
  a:active {
    color: #FFF;
  }


  // Menu header
  &__header {
    display: block;
    padding: 1.5rem 1.5rem 0;
    margin-bottom: -1.5rem;
    }
    // Mobile menu header divider
    &__header::after {
      content: "";
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      display: block;
      height: 1px;
      width: 100%;
      padding: 1.5rem 0 0;
      }
    &__header a {
      font-size: 24px;
      font-weight: 600;
    }


    // Menu body
    &__body {
      padding: 1.5rem;
    }

    // list style
    &__list {
      line-height: 55px;
      }
      // Mobile menu item divider
      &__list-item {
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      }

    // Link
    &__link {
      padding: 0;
      display: block;
      font-size: 18px;
      }
      &__link:hover {
        opacity: 0.5;
      }
  //
  }
//@media
}


/* SUB NAV
==================================================== */
.globalSubNav {
  display: block;
  line-height: normal;
  padding: 0;

  // Subnav link
  &__link,
  &__link:hover,
  &__link:active {
    color: #000;
    text-decoration: none;
  }

  // Store link
  &__link_store {
    background: transparent;
    border: 0;
    color: #000;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    padding: 0;

    // Copy
    span {
      display: none;
    }
    // icon
    svg {
      width: 24px;
      height: 24px;
    }
  }

  // Desktop
  @media screen and (min-width: $breakpoint-mobile-nav) {
    &__link_store {
      border: 1px solid;
      padding: 10px 22px;
      width: inherit;
      font-size: 16px;
      font-weight: 600;

      span {
        display: inline-block;
        margin-right: 15px;
      }

      svg {
        width: 22px;
        height: 22px;
        margin-top: -2px;
      }
    }
  }
  // XL
  @media screen and (min-width: $breakpoint-xxl) {
    &__link_store {
      font-size: 18px;
    }
  }
  // XXL
  @media screen and (min-width: $breakpoint-xxl) {
    &__link_store {
      font-size: 20px;
    }
  }
//
}



/* BACKDROP
==================================================== */
@media screen and (max-width: ($breakpoint-mobile-nav - 1) ) {
  .globalHeader__layout::after {
    content: "";
    box-sizing: border-box;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -10;
    visibility: hidden;
    opacity: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,.5);
    transition: all 350ms ease-in;
    // backdrop-filter: saturate(180%) blur(4px);
    }

    // Activated
    #globalHeader.opened .globalHeader__layout::after {
      z-index: 1040;
      opacity: 1;
      visibility: visible;
    }
  }



/* OFFCANVAS MOBILE MENU LEFT
==================================================== */
@media screen and (max-width: ($breakpoint-mobile-nav - 1) ) {
  #globalHeader.offcanvas-left .globalNav {
    left: 0;
    right: auto;
    transform: translateX(-100%);
  }

  #globalHeader.offcanvas-left .mobileMenuToggle::after {
    left: -60px;
    right: auto;
    transform: translateX(0);
  }

  #globalHeader.offcanvas-left.opened .globalNav {
    visibility: visible;
    transform: translateX(0%);
  }

  #globalHeader.offcanvas-left.opened .mobileMenuToggle::after {
    transition: all 340ms ease-in-out;
    transform: translateX($mobile-menu-width);
  }
//
}

@media screen and (max-width: 413px ) {
  #globalHeader.offcanvas-left .mobileMenuToggle::after {
    left: -100px;
  }
}

@media screen and (max-width: $mobile-menu-width) {
  #siteGlobalHeader .globalNav {
    max-width: 100%;
  }
  #siteGlobalHeader.offcanvas-left.opened .mobileMenuToggle::after {
    transform: translateX(100vw);
  }
 //
}



/* MENU ORDER
==================================================== */
@media screen and (max-width: ($breakpoint-mobile-nav - 1) ) {

  .mobileMenuToggle {
    min-width: 0;
    position: absolute;
    left: 0;
    top: 3px;
  }
  .globalHeaderLogo {
    margin: auto;
  }
  .globalSubNav {
    position: absolute;
    right: 0;
    top: 2px;
  }
}
