/* iOS disable focus zoom */
select,
textarea,
input,
input[type="color"],
input[type="date"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"] {
  font-size: 100%;
}


/* iOS style reset */
select,
textarea,
input[type="email"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="url"] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  border-radius: 0;
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"],
input[type="week"] {
  -webkit-border-radius: 0;
  border-radius: 0;
}


/* Remove focused outline */
select:focus,
input:focus,
textarea:focus {
  outline: none;
}

select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000;
} /* FF */


/* Safari date input vertical alignment fix */
input:is([type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"],
input[type="week"]) {
  align-items: center;
  display: inline-flex;
  overflow-x: hidden;
  overflow-y: hidden;
  // appearance: textfield;
  font-variant-numeric: tabular-nums;
  font-feature-settings: "ss03";
  height: 40px;
}

/* Safari number inout shows password fix */
input[type=number] {
  -webkit-text-security: none;
}




