/* ------------------------------------------------
   Form style
------------------------------------------------ */

/* Input field */
select,
textarea,
input[type="file"],
input[type="date"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
textarea {
  background-clip: padding-box;
  background-color: #fff;
  background-image: none;
  border-radius: 0;
  border: 1px solid #DADADA;
  box-sizing: border-box;
  caret-color: #888;
  color: #181818;
  display: inline-block;
  font-family: $font-sans;
  font-size: 16px;
  font-weight: normal;
  line-height: normal;
  margin-bottom: 12px;
  padding: 9px 12px;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

  &::placeholder {
    color: #666;
    opacity: 1; /* Firefox */
  }

  &:hover, &:focus {
    border-color: #181818;
    color: #181818;
  }
}


textarea {
  line-height: 1.5;
  resize: vertical;
}

