/* LIST */
ol,
ul {
    padding-left: 2rem;
    list-style: revert;
}

ol,
ul,
dl {
    margin-top: 0;
    margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
    margin-bottom: 0;
}

dt {
    font-weight: 700;
}

dd {
    margin-bottom: 0.5rem;
    margin-left: 0;
}





/* TABLE */
table {
    border-collapse: collapse;
    caption-side: bottom;
}

th {
    text-align: inherit;
    text-align: -webkit-match-parent;
}

thead,
tbody,
tfoot,
tr,
td,
th {
    border-color: inherit;
    border-style: solid;
    border-width: 0;
}

caption {
    opacity: 0.75;
    padding-bottom: 0.33rem;
    padding-top: 0.33rem;
    text-align: left;
}
