// Classic Typhograhic Scale
// 6, 7, 8, 9, 10, 11, 12, 14, 16, 18, 21, 24, 36, 48, 60, 72, 84, 96


$tscale10:  96px;
$tscale9:   84px;
$tscale8:   72px;
$tscale7:   60px;
$tscale6:   48px;
$tscale5:   36px;
$tscale4:   24px;
$tscale3:   21px;
$tscale2:   18px;
$tscale1:   16px;
$tscale-1:  14px;
$tscale-2:  12px;
$tscale-3:  11px;


h1 {
  font-size: 96px;
  letter-spacing: -1.5px;
}
h2 {
  font-size: 60px;
  letter-spacing: -0.5px;
}
h3 {
  font-size: 48px;
}
h4 {
  font-size: 36px;
}
h5 {
  font-size: 24px;
}
h6 {
  font-size: 21px;
}
p {
  font-size: 16px;
}
button {
  font-size: 16px;
}
small {
  font-size: 14px;
}
caption {
  font-size: 12px;
}
.footernote {
  font-size: 10px;
}





/* ===================================================
TYPOGRAPHY
==================================================== */
body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  color: #181818;
  font-family: $font-sans;
  font-size:   $base-font-size;
  line-height: $base-line-height;
  @media (min-width: $breakpoint-xxl) {
    font-size: 1.125rem; // 18px by default
    p,ul,dl {
      font-size: 1.125rem;
    }
  }
}



/* SELECTED TEXT */
::selection {
  background: #b3d4fc;
  text-shadow: none;
}



/* HEADING
==================================================== */
h1,h2,h3,h4,h5,h6,
.h0,.h1,.h2,.h3,.h4,.h5,.h6 {
  font-family: $font-display;
  font-weight: $font-bold-weight;
  line-height: 1.2;
  margin-bottom: 0.5rem;
  margin-top: 0;
}
h1,.h1 {
  font-size: $mscale_3;
}
h2,.h2 {
  font-size: $mscale_2;
}
h3,.h3 {
  font-size: $mscale_1;
}
h4,.h4 {
  font-size: $mscale_0;
}
h5,.h5 {
  font-size: $mscale_n1;
}
h6,.h6 {
  font-size: $mscale_n2;
}



/* BODY TEXT
==================================================== */
p {
  font-family: $font-sans;
  margin-top: 0;
  margin-bottom: 1rem;
  line-height: 1.5;
}

small,.small {
  font-size: 100% / $ratio;
}

blockquote {
  margin: 0 0 1rem;
}

b,strong {
  font-weight: $font-bold-weight;
}

mark {
  background-color: #fff3cd;
  padding: 0.1875rem;
}

sub,sup {
  font-size: $mscale_n1;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25rem;
}

sup {
  top: -0.5rem;
}

abbr[title] {
  cursor: help;
  text-decoration-skip-ink: none;
  text-decoration: underline dotted;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}



/* ETC
==================================================== */
pre,
code,
kbd,
samp {
  font-family: $font-monospace;
  font-size: 1em;
}

pre {
  display: block;
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  font-size: 0.875em;
}

pre code {
  font-size: inherit;
  color: inherit;
  word-break: normal;
}

code {
  font-size: 0.875em;
  color: #d63384;
  word-wrap: break-word;
}

a > code {
  color: inherit;
}

kbd {
  background-color:#F7F7F7;
  border-radius: 0.25rem;
  border: 1px solid #AAA;
  box-shadow:0 1px 0 rgba(0,0,0,0.2),0 0 0 1px #FFF inset;
  box-sizing: border-box;/*  */
  color: #333;
  font-size: 0.875em;
  font-weight: 600;
  padding: 0.063rem 0.375rem;/*  */
  text-shadow:0 1px 0 #FFF;
}

kbd kbd {
  border: 0;
  box-shadow: none;
  font-size: 1em;
  padding: 0;
  text-shadow: none;
  background-color: transparent;
}


body * {
  word-break: keep-all;
}
