/* ===================================================
SITE FOOTER
==================================================== */
.globalFooter {
  padding: 60px 0;
  width: 100%;
  line-height: 1.5;

  &::after {
    clear: both;
    content: "";
    display: table;
  }



/* Site Footer Menu
==================================================== */
  &__nav {
    display: block;
    list-style: none;
    margin: 0 auto 30px;
    padding: 0;
    text-align: center;
  }
  &__navItem {
    display: block;
    margin-bottom: 0.5rem;
  }

  &__navLink {
    display: block;
    text-decoration: underline;
  }



/* Site Footer notes
==================================================== */
  &__note,
  &__copyright {
    color: #888;
    margin: 0;
    text-align: center;
    width: 100%;
  }

  @media screen and (min-width: $content-width-lg) {
    & {
      padding-bottom: 50px;
    }

    &__nav,
    &__note,
    &__copyright {
      float: left;
      text-align: left;
      }
    &__navItem {
      display: inline-block;
      margin-right: 1rem;
    }
  // @media
  }

//.site-footer
}



/* Social Links
==================================================== */
.snsLink {
  display: block;
  list-style: none;
  margin: 0 0 24px;
  padding: 0;
  text-align: center;

  &__title {
    display: none;
    }
    strong {
      font-weight: 600;
    }

  &__item {
    display: inline-block;
    margin: 0 0.75rem;
    }
    &__link,
    &__link:hover,
    &__link:visited {
      color: #000;
      display: block;
      font-size: 0;
      text-decoration: none;
      width: 22px;
      }
      svg {
        height: auto;
        width: 100%;
      }

  @media screen and (min-width: $content-width-lg) {
    float: right;
    text-align: right;

    &__title {
      display: inline-block;
    }

    &__item {
      margin: 0 0 0 0.75rem;
      position: relative;
      top: -0.313rem;
    }
  }
//.site-social-links
}



/* Back to top
==================================================== */
.globalFooter__backTop {
  text-align: center;

  a {
    display: inline-block;
    margin: 30px auto 0;
    position: relative;
    text-align: center;
    text-decoration: none;
    background: #121b22;
    padding: 10px 20px 10px 10px;
    color: #FFF;
  }
  @media screen and (min-width: $content-width-lg) {
    a {
      bottom: 55px;
      margin: 0;
      position: fixed;
      right: 50px;
      text-align: right;
    }
  }
}
